import NumericRange from "./components/numeric-range";
import DateRange from "./components/date-range";
import FilterFieldToggle from "./components/filter-field-toggle";
import { initFilteringCache } from "./components/filtering-cache";
import { initTaxonomyFilters, initUserFilterButton, placeMsUserFilterMarkup } from "./components/filter-functions";
import { initActiveState } from "./helpers/active-state";
document.addEventListener('DOMContentLoaded', function () {
    initActiveState();
    document.querySelectorAll('.acp-range.number').forEach(function (container) { return new NumericRange(container); });
    document.querySelectorAll('.acp-range.date').forEach(function (container) { return new DateRange(container); });
    document.querySelectorAll('input[name=acp_filter_action]').forEach(function (el) { return initUserFilterButton(el); });
    document.querySelectorAll('.acp_tax_filters').forEach(function (el) { return initTaxonomyFilters(el); });
    var table = document.querySelector(AC.table_id);
    if (table) {
        new FilterFieldToggle(table.closest('form'));
    }
    initFilteringCache();
    if (document.body.classList.contains('ac-wp-ms_users')) {
        placeMsUserFilterMarkup();
    }
});
