import Axios from "axios";
import { mapDataToFormData } from "AC/helpers/global";
import { initAcServices } from "AC/helpers/admin-columns";
import { initActiveState } from "../helpers/active-state";
export var initFilteringCache = function () {
    var originalSelects = document.querySelectorAll('select.acp-filter');
    updateFilterCache().then(function (response) {
        var htmlResponse = document.createRange().createContextualFragment(response.data.data);
        var services = initAcServices();
        originalSelects.forEach(function (originalSelect) {
            var shadowSelect = htmlResponse.querySelector("select[name=\"" + originalSelect.name + "\"]");
            if (shadowSelect) {
                var disabled = Array.from(shadowSelect.children).find(function (option) { return option.value === '__ac_disabled_loading'; });
                if (disabled) {
                    return originalSelect.remove();
                }
                originalSelect.replaceWith(shadowSelect);
                var currentValue = originalSelect.dataset.current;
                if (currentValue !== '') {
                    shadowSelect.querySelectorAll("option[data-value=\"" + currentValue + "\"]").forEach(function (e) { return e.selected = true; });
                }
                if (services.hasService('ActiveFilters')) {
                    shadowSelect.addEventListener('change', function () { return services.getService('ActiveFilters').refresh(); });
                }
            }
            else {
                originalSelect.remove();
            }
        });
        initActiveState();
    }).catch(function () {
        originalSelects.forEach(function (el) { return el.remove(); });
    });
};
var updateFilterCache = function () {
    return Axios.post(ajaxurl, mapDataToFormData({
        action: 'acp_update_filtering_cache',
        list_screen: AC.list_screen,
        layout: AC.layout,
        _ajax_nonce: AC.ajax_nonce
    }));
};
